.container {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  cursor: pointer;
}

.disabledContainer {
  pointer-events: none;
}

.img {
  margin-right: 11px;
  cursor: pointer;
}

.text {
  color: #fff;
  font-family: PT_Root_UI_medium;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 20.16px;
}

.textDisabled {
  color: #676c7a;
}
